* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
}

body {
	background: #E3F2FD;
}
.chatbot-toggler {
	position: fixed;
	right: 40px;
	bottom: 35px;
	height: 50px;
	width: 50px;
	color: #fff;
	border: none;
	display: flex;
	align-items: center;
	justify-content: center;
	outline: none;
	cursor: pointer;
	background: #724ae8;
	border-radius: 50%;
	transition: all 0.2s ease;
}
.show-chatbot .chatbot-toggler {
	transform: rotate(90deg);
}
.chatbot-toggler span {
	position: absolute;
}
.show-chatbot .chatbot-toggler span:first-child,
.chatbot-toggler span:last-child {
	opacity: 0;
}
.show-chatbot .chatbot-toggler span:last-child {
	opacity: 1
}
.chatbot {
	position: fixed;
	right: 40px;
	bottom: 100px;
	width: 30vw;
	transform: scale(0.5);
	opacity: 0;
	pointer-events: none;
	overflow: hidden;
	background: #fff;
	border-radius: 15px;
	transform-origin: bottom-right;
	box-shadow: 0 0 128px 0 rgba(0,0,0,0.1), 0 32px 64px -48px rgba(0,0,0,0.5);
	transition: all 0.1s ease;
}
.show-chatbot .chatbot {
	transform: scale(1);
	opacity: 1;
	pointer-events: auto;
}
.chatbot header {
	background: #724ae8;
	padding: 16px 0;
	text-align: center;
	position: relative;
}
.chatbot header h2 {
	color: #fff;
	font-size: 1.4rem;
}
.chatbot header span {
	position: absolute;
	right: 20px;
	top: 50%;
	color: #fff;
	cursor: pointer;
	display: none;
	transform: translateY(-50%);
}
.chatbot .chatbox {
	height: 70vh;
	overflow-y: auto;
	padding: 30px 20px 100px;
}
.chatbox .chat {
	display: flex;
}
.chatbox .incoming span {
	height: 32px;
	width: 32px;
	color: #fff;
	align-self: flex-end;
	background: #724ae8;
	text-align: center;
	line-height: 32px;
	border-radius: 10px;
	margin: 0 10px 7px 0;
}
.chatbox .outgoing {
	margin: 20px 0;
	justify-content: flex-end;
}
.chatbox .chat p {
	color: #fff;
	max-width: 75%;
	white-space: pre-wrap;
	font-size: 0.95rem;
	padding: 12px 16px;
	border-radius: 10px 10px 0 10px;
	background: #724ae8;
}
.chatbox .chat p.error {
	color: #721c24;
	background: #f8d7da;
}
.chatbox .incoming p {
	color: #000;
	background: #f2f2f2;
	border-radius: 10px 10px 10px 0;
}
.chatbot .chat-input {
	position: absolute;
	bottom: 0;
	width: 100%;
	display: flex;
	gap: 5px;
	background: #fff;
	padding: 5px 20px;
	border-top: 1px solid #ccc;
}
.chat-input textarea {
	height: 55px;
	width: 100%;
	border: none;
	outline: none;
	max-height: 180px;
	font-size: 0.95rem;
	resize: none;
	padding: 16px 15px 16px 0;
}
.chat-input span {
	align-self: flex-end;
	height: 55px;
	line-height: 55px;
	color: #724ae8;
	font-size: 1.35rem;
	cursor: pointer;
	visibility: hidden;
}

.chat-input textarea:valid ~ span {
	visibility: visible;
}
@media(max-width: 490px){
	.chatbot {
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		border-radius: 0;
	}
	.chatbot .chatbox {
		height: 90%;
	}
	.chatbot header span {
		display: block;
	}
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,kCAAkC;AAElC;CACC,SAAS;CACT,UAAU;CACV,sBAAsB;CACtB,kCAAkC;AACnC;;AAEA;CACC,mBAAmB;AACpB;AACA;CACC,eAAe;CACf,WAAW;CACX,YAAY;CACZ,YAAY;CACZ,WAAW;CACX,WAAW;CACX,YAAY;CACZ,aAAa;CACb,mBAAmB;CACnB,uBAAuB;CACvB,aAAa;CACb,eAAe;CACf,mBAAmB;CACnB,kBAAkB;CAClB,yBAAyB;AAC1B;AACA;CACC,wBAAwB;AACzB;AACA;CACC,kBAAkB;AACnB;AACA;;CAEC,UAAU;AACX;AACA;CACC;AACD;AACA;CACC,eAAe;CACf,WAAW;CACX,aAAa;CACb,WAAW;CACX,qBAAqB;CACrB,UAAU;CACV,oBAAoB;CACpB,gBAAgB;CAChB,gBAAgB;CAChB,mBAAmB;CACnB,8BAA8B;CAC9B,0EAA0E;CAC1E,yBAAyB;AAC1B;AACA;CACC,mBAAmB;CACnB,UAAU;CACV,oBAAoB;AACrB;AACA;CACC,mBAAmB;CACnB,eAAe;CACf,kBAAkB;CAClB,kBAAkB;AACnB;AACA;CACC,WAAW;CACX,iBAAiB;AAClB;AACA;CACC,kBAAkB;CAClB,WAAW;CACX,QAAQ;CACR,WAAW;CACX,eAAe;CACf,aAAa;CACb,2BAA2B;AAC5B;AACA;CACC,YAAY;CACZ,gBAAgB;CAChB,wBAAwB;AACzB;AACA;CACC,aAAa;AACd;AACA;CACC,YAAY;CACZ,WAAW;CACX,WAAW;CACX,oBAAoB;CACpB,mBAAmB;CACnB,kBAAkB;CAClB,iBAAiB;CACjB,mBAAmB;CACnB,oBAAoB;AACrB;AACA;CACC,cAAc;CACd,yBAAyB;AAC1B;AACA;CACC,WAAW;CACX,cAAc;CACd,qBAAqB;CACrB,kBAAkB;CAClB,kBAAkB;CAClB,+BAA+B;CAC/B,mBAAmB;AACpB;AACA;CACC,cAAc;CACd,mBAAmB;AACpB;AACA;CACC,WAAW;CACX,mBAAmB;CACnB,+BAA+B;AAChC;AACA;CACC,kBAAkB;CAClB,SAAS;CACT,WAAW;CACX,aAAa;CACb,QAAQ;CACR,gBAAgB;CAChB,iBAAiB;CACjB,0BAA0B;AAC3B;AACA;CACC,YAAY;CACZ,WAAW;CACX,YAAY;CACZ,aAAa;CACb,iBAAiB;CACjB,kBAAkB;CAClB,YAAY;CACZ,yBAAyB;AAC1B;AACA;CACC,oBAAoB;CACpB,YAAY;CACZ,iBAAiB;CACjB,cAAc;CACd,kBAAkB;CAClB,eAAe;CACf,kBAAkB;AACnB;;AAEA;CACC,mBAAmB;AACpB;AACA;CACC;EACC,QAAQ;EACR,SAAS;EACT,WAAW;EACX,YAAY;EACZ,gBAAgB;CACjB;CACA;EACC,WAAW;CACZ;CACA;EACC,cAAc;CACf;AACD","sourcesContent":["/* Import Google Fonts - Poppins */\n@import url(\"https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap\");\n* {\n\tmargin: 0;\n\tpadding: 0;\n\tbox-sizing: border-box;\n\tfont-family: \"Poppins\", sans-serif;\n}\n\nbody {\n\tbackground: #E3F2FD;\n}\n.chatbot-toggler {\n\tposition: fixed;\n\tright: 40px;\n\tbo